// nkym, jossa listataan ostoert 

import { Button, Container, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Summa from './Summa';


//----------------------------interfaces --------------------------------------------------------------
interface Props {
    ostoErat: OstoEra[];
    setOstoErat : (arg0 : OstoEra[]) => void;
    }

// --------------------------- funktiot ---------------------------------------------------------------
    function createData(
      ostoIDTauluun: string,
      osakeTickerTauluun: string,
      osakeNimiTauluun: string,
      maaraTauluun: number,
      ostoHintaTauluun: number,
      nykyArvoTauluun: number,
      arvonMuutosEURTauluun : number,
      arvonMuutosProsTauluun: number,
      kokonaisArvoTauluun : number
      ) {
      return {  ostoIDTauluun, 
                osakeTickerTauluun, 
                osakeNimiTauluun, 
                maaraTauluun, 
                ostoHintaTauluun, // ostohinta per kpl
                nykyArvoTauluun, // nykyarvo per kpl
                arvonMuutosEURTauluun, 
                arvonMuutosProsTauluun, 
                kokonaisArvoTauluun  // maara kpl * nykyarvo eur/kpl = arvo eur
              };
    }

    let salkunArvoTemp : number = 0;

// --------------------------------- lista funktio -----------------------------------------------------------
const Lista : React.FC<Props> = ({ostoErat, setOstoErat}) : React.ReactElement => {

      const [salkunArvo, setSalkunArvo] = useState<number>(); // salkun kokonaisarvo

      const [salkunKokonaisMuutos, setSalkunKokonaisMuutos] = useState<number>(); // salkun kokonaismuutos

      const [data, setData] = useState<Data>({    // haettava data
        kurssiTiedot : {},
        virhe : "",
        valmis : false
      });

      let merkkiJonoHakuun : string;  //thn parsitaan merkkijono 


// Tm alla oleva koodi suoritetaan vain kerran komponentin ensimmisen renderinnin jlkeen

useEffect(() => {
      console.log('Tm koodi suoritetaan vain kerran');
      

     
        //--------------------- ostoerist parsitaan osakkeen tikkereist kysely Yahoo Financeen -----------------------------------

        function muodostaMerkkijono(ostoErat: OstoEra[]): string {
          const osakeTickeritSet: Set<string> = new Set();  // set, koska sama tikkeri voi esiinty useammassa ostoerss
          ostoErat.forEach(era => osakeTickeritSet.add(era.osakeTicker));

          const osakeTickerit: string[] = Array.from(osakeTickeritSet);

          const merkkijono = osakeTickerit.map((ticker, index) => {
                if (index === osakeTickerit.length - 1) {
                    return ticker + ".HE"; // viimeinen ticker ilman pilkkua, .HE = Helsingin porssi
                } else {
                    return ticker + ".HE,"; // Muut tickerit pilkulla erotettuna
                }
            }).join(""); // Yhdistetn osakeTickerit yhdeksi merkkijonoksi

        return merkkijono;
        }

        const muodostettuMerkkijono = muodostaMerkkijono(ostoErat);
        //console.log(muodostettuMerkkijono); 

        merkkiJonoHakuun = muodostettuMerkkijono;
    

        // --------------------------------- salkun kokonaisarvon laskenta ------------------------------------------
          // Lasketaan salkun arvo summaamalla kaikki era.maara * hintaNyt -arvot rows-taulukosta
              const uusiSalkunArvo = rows.reduce((acc, row) => acc + row.maaraTauluun * row.nykyArvoTauluun, 0);
              //console.log("Salkun arvo on:"+uusiSalkunArvo)

              // samalla lasketaan uusi kokonaisarvonmuutos
                const uusiSalkunMuutos = rows.reduce((muutos, row) => muutos + row.maaraTauluun * (row.nykyArvoTauluun - row.ostoHintaTauluun), 0);

              if (uusiSalkunArvo !== salkunArvoTemp) { // Pivitetn salkun arvo ja muutos vain, jos se on muuttunut
                setSalkunArvo(uusiSalkunArvo); 
                setSalkunKokonaisMuutos(uusiSalkunMuutos);
              }

        
   
        //--------------------------------- haetaan kurssidata -----------------------------------------------------
     
              const haeData = async () : Promise<void> => {

                const url = 'https://yahoo-finance127.p.rapidapi.com/multi-quote/'+merkkiJonoHakuun;

                console.log("URL= "+url);
                console.log("Valmis? "+data.valmis);

                const options = {
                    method: 'GET',
                    headers: {
                        'X-RapidAPI-Key': 'a02a974489msh636ecb079d7b49fp1015bbjsn2a3ae351251c', // key unique
                        'X-RapidAPI-Host': 'yahoo-finance127.p.rapidapi.com'
                    }
                };

                try {

                  if (!data.valmis) {

                    console.log("Muodostetaan yhteys...");

                  const yhteys : Response = await fetch (url, options);
                  const data : any = await yhteys.json();

                  setData({...data, valmis : true});


              const osakeTieto: { tikkeri: string, bid: number }[] = Object.keys(data).map((key: string) => {
                const osake = data[key];
                const tikkeri = osake.symbol.slice(0, -3);
                return {
                    tikkeri: tikkeri,
                    bid: osake.bid.raw  //tss hinta
                };
            });

                // Muodostetaan ostoEra uudestaan listen viimeisinHinta
                const uusiOstoEra: OstoEra[] = ostoErat.map(era => {
                  //.log("uusiOstoEra");
                  const viimeisinHinta = osakeTieto.find(osake => osake.tikkeri === era.osakeTicker)?.bid || 0;
                  return { ...era, viimeisinHinta };
                });

                setOstoErat(uusiOstoEra);


              console.log("Ostoert muunnoksen jlkeen: ", ostoErat);
              

              console.log(osakeTieto);

            } else {   // data.valmis = true.... data on jo haettu

              console.log("Mielestni on jo haettu..."+data.valmis)

            }        
               


                } catch (e : any) {
                  setData({...data, virhe: "Palvelimelle ei saada yhteytt.", valmis:false});

                  console.error("Palvelimelle ei saada yhteytt");
                }

              }

           haeData();

           console.log("Ostoert tss: ", ostoErat);
        

}, [data,ostoErat]); // Tyhj riippuvuusluettelo merkitsee, ett koodi suoritetaan vain kerran



// ---------------------------------- ostoerist arvot nytettvn tableen  -----------------------------------------------

const rows = ostoErat.map(era => createData(
  era.eraID,
  era.osakeTicker,  //tikkeri
  era.osakeNimi,    // yhtin nimi
  era.maara,        // ostoern kappalemr
  era.ostoHinta,    // ostoern ostohinta eur/kpl
  era.viimeisinHinta, // haettu viimeisin hinta
  (era.viimeisinHinta-era.ostoHinta)*era.maara,
  ((era.viimeisinHinta-era.ostoHinta)/era.ostoHinta)*100,
  era.maara * era.viimeisinHinta
));





  return (
  
//---------------------- JSX alkaa -------------------------------------------------------------
    <>
    <Container maxWidth='lg'>

      <Typography variant="h4">Salkkuseuranta</Typography>
      
      <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow
            sx={{backgroundColor: 'lightgray'}}>
            <TableCell>Tunnus</TableCell>
            <TableCell
              >Osakkeen nimi</TableCell>
            <TableCell align="right">Mr</TableCell>
            <TableCell align="right">Ostohinta</TableCell>
            <TableCell align="right">Viimeisin Eur</TableCell>
            <TableCell align="right">Arvo EUR</TableCell>
            <TableCell align="right">Tuotto EUR</TableCell>
            <TableCell align="right">Tuotto %</TableCell>
          </TableRow>
        </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.ostoIDTauluun} style={{ backgroundColor: row.arvonMuutosEURTauluun < 0 ? 'lightpink' : 'darkseagreen' }}>
                <TableCell component="th" scope="row">
                  {row.osakeTickerTauluun}
                </TableCell>
                <TableCell align="left">
                  <Link to={`/poista/${row.ostoIDTauluun}`}>
                    {row.osakeNimiTauluun}
                  </Link>
                </TableCell>
                <TableCell align="right">{row.maaraTauluun}</TableCell>
                <TableCell align="right">{row.ostoHintaTauluun.toFixed(2)}</TableCell>
                <TableCell align="right">{row.nykyArvoTauluun}</TableCell>
                <TableCell align='right'>{row.kokonaisArvoTauluun.toFixed(2)}</TableCell>
                <TableCell align="right">{row.arvonMuutosEURTauluun.toFixed(2)}</TableCell>
                <TableCell align="right">{row.arvonMuutosProsTauluun.toFixed(2)} %</TableCell>
              </TableRow>
            ))}
          </TableBody>



      </Table>
    </TableContainer>

    <div>
        <Button 
            variant="outlined"
            sx={{marginTop: "10px"}}
            component={Link}
            to="/uusi"
            >Uusi ostoer salkkuun</Button>

      </div>
      
          

      <Summa arvo={salkunArvo} kokonaisMuutos={salkunKokonaisMuutos}/>
      </Container>
    </>
  )
}

export default Lista
