// Tulostetaan koko salkun arvo ja muutos

import { Typography } from '@mui/material'
import React from 'react'

interface Props {
    arvo : number | undefined;
    kokonaisMuutos : number | undefined;
}

const Summa = (props : Props) => {

    const tulostettavaArvo = props.arvo?.toFixed(2);
    const tulostettavaMuutos = props.kokonaisMuutos?.toFixed(2);
  return (
<div style={{ display: 'flex', justifyContent: 'flex-end' }}>
  <div style={{ textAlign: 'right' }}>
    <Typography variant="h6">Salkun kokonaisarvo on {tulostettavaArvo} euroa</Typography>
    <Typography variant="h6" style={{ color: props.kokonaisMuutos! <= 0 ? 'red' : 'green' }}>
      {props.kokonaisMuutos! <= 0 ? 'Muutos ' : <br />} {tulostettavaMuutos} euroa
    </Typography>
  </div>
</div>

  )
}

export default Summa
