// ostoern poistomahdollisuus, vahvistuskysymys kyttjlle

import { Button, Container, OutlinedInput, Typography } from '@mui/material';
import React, { useEffect, useRef, useState } from 'react'
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';

// ---------------- interfaces ------------------------------------------------------
interface Props {
    ostoErat : OstoEra[]
    setOstoErat : (arg0 : OstoEra[]) => void
}

const PoistaOstoEra : React.FC<Props> = ({ ostoErat, setOstoErat }) : React.ReactElement => {

    //console.log(ostoErat);

    const navigate : NavigateFunction = useNavigate();

    const { eraID } = useParams<string>();



// ---------------------- poistoon.eraID sislt poistettavan ostoeran ---------------------
    const poistettavaOstoEra : OstoEra | undefined = ostoErat.find((poistoon : OstoEra) => {
        return poistoon.eraID === eraID;
            });

// --------------------- funktio poistoon --------------------------------------------------
const vahvistaPoisto = () : void => {
    
    setOstoErat([...ostoErat.filter((ostoErat : OstoEra) => ostoErat.eraID !== eraID)]);

    navigate("/");
}


return (
    <>
     <Container maxWidth="sm">
                    
                    <Typography
                        sx={{marginBottom : "20px", textAlign: "center"}}
                        >Poistetaanko ostettu osake-er: {poistettavaOstoEra!.osakeNimi}?</Typography>

                        <Button
                            variant="contained"
                            sx={{marginBottom: "5px"}}
                            fullWidth={true}
                            onClick={vahvistaPoisto}
                        >Poista</Button>  

                        <Button
                            fullWidth={true}
                            variant="outlined"
                            component={Link}
                            to="/"
                        >Peruuta</Button>    
        </Container>
    
    </>

)


}

export default PoistaOstoEra