// lomake uuden ostoern lismiseen

import { Button, Container, FormHelperText, Grid, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Typography } from '@mui/material';
import React, { useEffect, useRef, useState } from 'react'
import { NavigateFunction, useNavigate } from 'react-router-dom';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';
import { DatePicker } from '@mui/x-date-pickers';
import osakelistaus from '../osakelistaus.json'

// ---------------- interfaces ------------------------------------------------------
interface Props {
    ostoErat : OstoEra[]
    setOstoErat : (arg0 : OstoEra[]) => void
}

// lomakkeen kentt virheentarkituksiin:
interface FormInformation {
    [key : string] : any;
    tikkeri?: string;
    nimi? : string;
    maara? : number;
    ostoarvo? : number;
    ostopaiva? : Date;
}

// virheet
interface FormErrors extends FormInformation{};

let osakeValittu : boolean = false; // kontrolloidaan 'Lis salkkuun nappia'

const UusiOstoEra : React.FC<Props> = ({ostoErat, setOstoErat}) : React.ReactElement => {

    const navigate : NavigateFunction = useNavigate();

    const uusiNimiRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiMaaraRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiOstoHintaRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

    const [ostopvm, setOstopvm] = useState<Date | null>();
    const [valittuTikkeri, setValittuTikkeri] = useState<string | null>();
    const [valittuNimi, setValittuNimi] = useState<string>();

    const formInformation : FormInformation = useRef<FormInformation>({});
    const [virheIlmoitukset, setVirheIlmoitukset] = useState<FormErrors>({});

    const [annettuMaara, setAnnettuMaara] = useState<number>(0); // virheentarkistuksen muuttujat
    const [ostohinta, setOstohinta] = useState<number>(0);

// ----------------------------- funktio tikkerin perusteella nimen hakuun ------------------------------------

const tikkerinMuutos = (e: SelectChangeEvent) => {
    setValittuTikkeri(e.target.value);
  };

const haeOsakkeenNimi = (tikkeri : string ) => {
const osakeLista : OsakeLista = osakelistaus.tikkeri;
if (osakeLista.hasOwnProperty(tikkeri)) {
    const nimi: string = osakeLista[tikkeri];
    setValittuNimi(nimi);
    } else {
    setValittuNimi('Osaketta ei lytynyt');
    }
}

useEffect(() => {
    if (valittuTikkeri) {
      haeOsakkeenNimi(valittuTikkeri);
      osakeValittu = true;
    }
  }, [valittuTikkeri]);

//---------------------- funktio lauantain ja sunnuntain etsimiseen (blokataan DatePickeriss) ---------------

const piilotaPaivat = (date: Date) => {
    // Tarkista, onko valittu piv lauantai (6) tai sunnuntai (0)
    return date.getDay() === 0 || date.getDay() === 6;
  };

// ----------------------------- funktio ostoern lisykseen ---------------------------------------------------

const lisaaOstoEra = () : void => {

    let apuMuuttujaOstoEra : OstoEra = { //tallennetaan vliaikaisesti arvot lomakkeelta
        eraID : uuid(),
        osakeTicker : valittuTikkeri || "n/a",
        osakeNimi : uusiNimiRef.current!.value || "n/a",
        maara : Number(uusiMaaraRef.current!.value) || 0,
        ostoHinta : Number(uusiOstoHintaRef.current!.value) || 0,
        ostoPaiva : new Date(),
        viimeisinHinta : (1)

    }

    setOstoErat ([...ostoErat, apuMuuttujaOstoEra]); 

    osakeValittu = false;

    navigate("/"); // paluu juureen
}

const lisayksenPeruutus = () : void => {

    osakeValittu = false;

    navigate("/"); // paluu juureen

}
    
// ----------------------------- formHandler ------------------------------------------------------------------

    const formHandler = ( e : React.FormEvent) : void => {
 
    e.preventDefault();

// ----------------- onko lomakkeessa virheit? --------------------------------------------------------------

    let virheet : FormErrors = {};


    if (!osakeValittu) {
        virheet = {...virheet, errorTikkeri : "Osaketta ei valittu"}
        console.error("Osaketta ei valittu.."+osakeValittu)
    }

    if (annettuMaara < 1 ) {
        virheet = {...virheet, errorMaara : "Tarkasta ostettu mr"}
        console.error("Mr liian pieni..")
    }    
    
    if (ostohinta <= 0.01 ) {
        virheet = {...virheet, errorOstohinta : "Tarkasta ostohinta"}
        console.error("Ostohinta liian pieni..")
    }

  
    if (Object.entries(virheet).length > 0) {
        setVirheIlmoitukset({...virheet});
        console.log("Virheit lytyi");


    
    
    } else { // ---------------------- lomakkeessa ei virheit

    setVirheIlmoitukset({});

    lisaaOstoEra();

    }


}


  return (
//------------------------------------ JSX alkaa ----------------------------------------------------------------

    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>

    <Container maxWidth='lg'>
    
        <Typography variant="h5"
            >Listn uusi ostoer salkkuun</Typography>

        <form onSubmit={formHandler}>
        
        <Grid container spacing={2}>
            {/* .........................tikkeri..................... */}
            <Grid item xs={4}>
                <InputLabel
                    style={{backgroundColor: '#fff', paddingLeft: 5, paddingRight: 5}}
                    >Tunnus</InputLabel>

                <Select
                    name="tunnus"
                    variant='outlined'
                    defaultValue='_'
                    fullWidth
                    onChange={tikkerinMuutos}
                    sx={{marginTop: "0px"}}
                    error={Boolean(virheIlmoitukset.errorTikkeri)}
                    
                    >
                {Object.keys(osakelistaus.tikkeri).sort().map((tikkeri:string, idx:number)  => {
                    return <MenuItem key={tikkeri} value={tikkeri}>{tikkeri}</MenuItem>
                }
                
                )}
                    
                </Select>
                {virheIlmoitukset.errorTikkeri ? <FormHelperText sx={{color: 'red'}}>{virheIlmoitukset.errorTikkeri}</FormHelperText> : null}
            </Grid>

            {/* ..................... nimi .......................... */}
            <Grid item xs={8}>
                <InputLabel
                    style={{backgroundColor: '#fff', paddingLeft: 5, paddingRight: 5}}
                    >Nimi</InputLabel>

                <TextField
                    disabled
                    variant="outlined"
                    value={valittuNimi}
                    fullWidth
                    inputRef={uusiNimiRef}></TextField>
            </Grid>

            {/*...................... ostettu mr ................ */}
            <Grid item xs={4}>
                <TextField
                    label="Mr"
                    name="annettuMaara"
                    variant="outlined"
                    fullWidth
                    onChange={(e) => {
                        // Muunnetaan syte numeroksi
                        const maara = parseFloat(e.target.value);
                        // Tarkistetaan, ett syte on numero
                        if (!isNaN(maara)) {
                            setAnnettuMaara(maara); // Aseta numeroksi muunnettu mr
                        } else {
                            setAnnettuMaara(0); // Tyhjenn mr, jos syte ei ole numero
                        }
                    }}
                    error={Boolean(virheIlmoitukset.errorMaara)}
                    helperText = {virheIlmoitukset.errorMaara}
                    inputRef={uusiMaaraRef}></TextField>
            </Grid>

            {/*....................... ostohinta.................... */}
            <Grid item xs={4}>
                <TextField
                    label="Ostohinta"
                    variant="outlined"
                    name="ostohinta"
                    error={Boolean(virheIlmoitukset.errorOstohinta)}
                    helperText = {virheIlmoitukset.errorOstohinta}
                    fullWidth
                    defaultValue={0}
                    inputRef={uusiOstoHintaRef}
                    onChange={(e) => { setOstohinta(parseFloat(e.target.value)) }}
       
                    ></TextField>
            </Grid>
            
            {/* ....................... ostopvm .................... */}
            <Grid item xs={4}>
                <DatePicker
                    disableFuture
                    label='Ostopivmr'
                    name='ostopvm'
                    shouldDisableDate={piilotaPaivat}
                    value={ostopvm}
                    defaultValue={null}
                    onChange={(ostopvm : Date | null) => setOstopvm(ostopvm!)}
                    sx={{width: '100%'}}
                    />
            </Grid>
    
        </Grid>
            <Button
                type='submit'
                disabled = {!osakeValittu}
                variant='contained'>Lisaa salkkuun</Button>

            <Button
                variant='outlined'
                onClick={lisayksenPeruutus}>Paluu</Button>

        </form>
        </Container>    
    </LocalizationProvider>
  )
}


export default UusiOstoEra;
