import React, { useEffect, useRef, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import Lista from './components/Lista';
import UusiOstoEra from './components/UusiOstoEra';
import PoistaOstoEra from './components/PoistaOstoEra';

const App : React.FC = () : React.ReactElement => {

  const kaynnistetty : React.MutableRefObject<boolean> = useRef(false); // LocalStoragen lukua varten 

  const [ostoErat, setOstoErat] = useState<OstoEra[]>([]);

// luetaan Local Storage -arvot, kun ohjelma kynnistyy..................................................................  
useEffect(() => {

  if (!kaynnistetty.current)  {

    if (localStorage.getItem("salkkusisalto")) {

      setOstoErat(JSON.parse(String(localStorage.getItem("salkkusisalto"))).map((ostoera : OstoEra) => {
        return {
          ...ostoera
        }
      }))
    }

  }

  return () => {
    kaynnistetty.current = true;
  }

}, []);

  // kirjoitetaan Local Storage -arvot, kun muutos ostoeriss ---------------------------------------------------------

  useEffect(() => {

    localStorage.setItem("salkkusisalto", JSON.stringify(ostoErat));

  }, [ostoErat]);

// ------------------- JSX alkaa -------------------------------------------------------------------------

  return (
    <>

    <Routes>
      
   
       <Route path="/" element={<Lista ostoErat={ostoErat} setOstoErat={setOstoErat} />} />

       <Route path="/uusi" element={<UusiOstoEra ostoErat={ostoErat} setOstoErat={setOstoErat} />} />

       <Route path="/poista/:eraID" element={<PoistaOstoEra ostoErat={ostoErat} setOstoErat={setOstoErat} />} />


  
    </Routes>
    </>
  );
}

export default App;
